import { useState, useRef, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { useLocation } from "react-router-dom";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";
import { Slider } from "@/components/ui/slider";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { useSiteSettings } from "@/hooks/useSiteSettings";
import { Upload, Image, Loader2, Phone, MessageSquare, Mail, Users, Mic, Link2, Info, Globe, Ghost } from "lucide-react";
import { Switch } from "@/components/ui/switch";
import { supabase } from "@/integrations/supabase/client";
import { useQueryClient } from "@tanstack/react-query";
import logoFallback from "@/assets/xiaoxiong-logo-christmas.png";

const AdminSiteSettings = () => {
  const { t } = useTranslation();
  const location = useLocation();
  const queryClient = useQueryClient();
  const { settings, isLoading, updateSettings, uploadLogo } = useSiteSettings();
  const [uploading, setUploading] = useState(false);
  const [uploadingFavicon, setUploadingFavicon] = useState(false);
  const logoInputRef = useRef<HTMLInputElement>(null);
  const faviconInputRef = useRef<HTMLInputElement>(null);
  const joinUsRef = useRef<HTMLDivElement>(null);
  const contactInfoRef = useRef<HTMLDivElement>(null);
  
  // Local state for sliders to enable real-time preview
  const [localWidth, setLocalWidth] = useState(35);
  const [localHeight, setLocalHeight] = useState(35);

  // Local state for join us settings
  const [joinPhone, setJoinPhone] = useState("");
  const [joinTitleZh, setJoinTitleZh] = useState("");
  const [joinTitleEn, setJoinTitleEn] = useState("");
  const [joinTitlePt, setJoinTitlePt] = useState("");
  const [joinMessageZh, setJoinMessageZh] = useState("");
  const [joinMessageEn, setJoinMessageEn] = useState("");
  const [joinMessagePt, setJoinMessagePt] = useState("");

  // Local state for contact info
  const [contactPhone, setContactPhone] = useState("");
  const [contactEmail, setContactEmail] = useState("");

  // Local state for SEO settings
  const [siteTitleZh, setSiteTitleZh] = useState("");
  const [siteTitleEn, setSiteTitleEn] = useState("");
  const [siteTitlePt, setSiteTitlePt] = useState("");
  const [siteDescriptionZh, setSiteDescriptionZh] = useState("");
  const [siteDescriptionEn, setSiteDescriptionEn] = useState("");
  const [siteDescriptionPt, setSiteDescriptionPt] = useState("");

  // Local state for ghost intro content
  const [ghostConceptZh, setGhostConceptZh] = useState("");
  const [ghostConceptEn, setGhostConceptEn] = useState("");
  const [ghostConceptPt, setGhostConceptPt] = useState("");
  const [ghostAdvantages, setGhostAdvantages] = useState<Array<{
    title_zh: string; title_en: string; title_pt: string;
    desc_zh: string; desc_en: string; desc_pt: string;
  }>>([]);
  useEffect(() => {
    if (settings) {
      setLocalWidth(settings.logo_width || 35);
      setLocalHeight(settings.logo_height || 35);
      setJoinPhone(settings.join_contact_phone || "968815050");
      setJoinTitleZh(settings.join_success_title_zh || "信息已提交成功！");
      setJoinTitleEn(settings.join_success_title_en || "Request Submitted!");
      setJoinTitlePt(settings.join_success_title_pt || "Informação Enviada com Sucesso!");
      setJoinMessageZh(settings.join_success_message_zh || "感谢您的信任！我们的专属顾问将在24小时工作日内通过电话与您联系。");
      setJoinMessageEn(settings.join_success_message_en || "Thank you for your trust! Our dedicated consultant will contact you by phone within 24 business hours.");
      setJoinMessagePt(settings.join_success_message_pt || "Obrigado pela sua confiança! Nosso consultor dedicado entrará em contato por telefone dentro de 24 horas úteis.");
      setContactPhone(settings.contact_phone || "");
      setContactEmail(settings.contact_email || "");
      // SEO settings
      setSiteTitleZh(settings.site_title_zh || "小熊市场 - 发现美食之旅");
      setSiteTitleEn(settings.site_title_en || "Xiaoxiong Market - Discover Amazing Restaurants");
      setSiteTitlePt(settings.site_title_pt || "Xiaoxiong Market - Descubra Restaurantes Incríveis");
      setSiteDescriptionZh(settings.site_description_zh || "探索精选餐厅，参与美食活动，与美食爱好者联系。");
      setSiteDescriptionEn(settings.site_description_en || "Explore curated restaurants, join exciting dining events, and connect with food lovers.");
      setSiteDescriptionPt(settings.site_description_pt || "Explore restaurantes selecionados, participe de eventos gastronômicos.");
      // Ghost intro
      const gc = settings.ghost_intro_content;
      if (gc) {
        setGhostConceptZh(gc.concept_zh || "");
        setGhostConceptEn(gc.concept_en || "");
        setGhostConceptPt(gc.concept_pt || "");
        setGhostAdvantages(gc.advantages || []);
      }
    }
  }, [settings]);

  // Handle hash navigation for join-us and contact-info sections
  useEffect(() => {
    if (location.hash === "#join-us" && joinUsRef.current) {
      setTimeout(() => {
        joinUsRef.current?.scrollIntoView({ behavior: "smooth", block: "start" });
      }, 100);
    } else if (location.hash === "#contact-info" && contactInfoRef.current) {
      setTimeout(() => {
        contactInfoRef.current?.scrollIntoView({ behavior: "smooth", block: "start" });
      }, 100);
    }
  }, [location.hash, isLoading]);

  const handleLogoUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    setUploading(true);
    try {
      const url = await uploadLogo(file);
      if (url) {
        await updateSettings.mutateAsync({ logo_url: url });
      }
    } finally {
      setUploading(false);
    }
  };

  const handleFaviconUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    setUploadingFavicon(true);
    try {
      const url = await uploadLogo(file);
      if (url) {
        await updateSettings.mutateAsync({ favicon_url: url });
      }
    } finally {
      setUploadingFavicon(false);
    }
  };

  const handleWidthChange = (value: number[]) => {
    setLocalWidth(value[0]);
  };

  const handleHeightChange = (value: number[]) => {
    setLocalHeight(value[0]);
  };

  const handleWidthCommit = async (value: number[]) => {
    await updateSettings.mutateAsync({ logo_width: value[0] });
  };

  const handleHeightCommit = async (value: number[]) => {
    await updateSettings.mutateAsync({ logo_height: value[0] });
  };

  const handleWidthInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = parseInt(e.target.value) || 20;
    const clampedValue = Math.min(Math.max(value, 20), 100);
    setLocalWidth(clampedValue);
  };

  const handleWidthInputBlur = async () => {
    await updateSettings.mutateAsync({ logo_width: localWidth });
  };

  const handleHeightInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = parseInt(e.target.value) || 20;
    const clampedValue = Math.min(Math.max(value, 20), 100);
    setLocalHeight(clampedValue);
  };

  const handleHeightInputBlur = async () => {
    await updateSettings.mutateAsync({ logo_height: localHeight });
  };

  const handleRemoveLogo = async () => {
    await updateSettings.mutateAsync({ logo_url: null });
  };

  const handleRemoveFavicon = async () => {
    await updateSettings.mutateAsync({ favicon_url: null });
  };

  const handleSaveJoinSettings = async () => {
    await updateSettings.mutateAsync({
      join_contact_phone: joinPhone,
      join_success_title_zh: joinTitleZh,
      join_success_title_en: joinTitleEn,
      join_success_title_pt: joinTitlePt,
      join_success_message_zh: joinMessageZh,
      join_success_message_en: joinMessageEn,
      join_success_message_pt: joinMessagePt,
    });
  };

  const handleSaveContactInfo = async () => {
    await updateSettings.mutateAsync({
      contact_phone: contactPhone,
      contact_email: contactEmail,
    });
  };

  const handleSaveSEOSettings = async () => {
    await updateSettings.mutateAsync({
      site_title_zh: siteTitleZh,
      site_title_en: siteTitleEn,
      site_title_pt: siteTitlePt,
      site_description_zh: siteDescriptionZh,
      site_description_en: siteDescriptionEn,
      site_description_pt: siteDescriptionPt,
    });
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center h-64">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
      </div>
    );
  }

  const currentLogo = settings?.logo_url || logoFallback;

  const isCommunityEnabled = settings?.community_enabled !== false;

  const handleToggleCommunity = async () => {
    const newValue = !isCommunityEnabled;
    
    // 1. 更新网站设置
    await updateSettings.mutateAsync({ community_enabled: newValue });
    
    // 2. 同步更新导航按钮
    const { data: communityButton } = await supabase
      .from("navigation_buttons")
      .select("id")
      .eq("link", "/community")
      .single();
    
    if (communityButton) {
      await supabase
        .from("navigation_buttons")
        .update({ enabled: newValue })
        .eq("id", communityButton.id);
      
      // 刷新导航按钮缓存
      queryClient.invalidateQueries({ queryKey: ["navigation-buttons"] });
    }
  };

  return (
    <>
      <div className="space-y-6">
        <div>
          <h1 className="text-3xl font-bold">{t("admin.siteSettings")}</h1>
          <p className="text-muted-foreground">{t("admin.siteSettingsDescription")}</p>
        </div>

        {/* Feature Toggles */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Users className="h-5 w-5" />
              {t("admin.featureToggles", "功能开关")}
            </CardTitle>
            <CardDescription>
              {t("admin.featureTogglesDescription", "控制网站功能的显示与隐藏")}
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            {/* Community Toggle */}
            <div className="flex items-center justify-between p-4 bg-muted/30 rounded-lg">
              <div className="space-y-0.5">
                <Label htmlFor="community-enabled" className="text-base font-medium">
                  {t("admin.communityEnabled", "社区功能")}
                </Label>
                <p className="text-sm text-muted-foreground">
                  {isCommunityEnabled 
                    ? t("admin.communityEnabledDesc", "社区入口在导航栏中显示")
                    : t("admin.communityDisabledDesc", "社区入口已隐藏")}
                </p>
                <p className="text-xs text-muted-foreground flex items-center gap-1 mt-1">
                  <Link2 className="h-3 w-3" />
                  {t("admin.communitySyncHint", "此设置与导航按钮中的社区按钮同步")}
                </p>
              </div>
              <Switch
                id="community-enabled"
                checked={isCommunityEnabled}
                onCheckedChange={handleToggleCommunity}
                disabled={updateSettings.isPending}
              />
            </div>

            {/* Mobile About Us Button Toggle */}
            <div className="flex items-center justify-between p-4 bg-muted/30 rounded-lg">
              <div className="space-y-0.5">
                <div className="flex items-center gap-2">
                  <Info className="h-4 w-4 text-primary" />
                  <Label htmlFor="about-us-mobile" className="text-base font-medium">
                    {t("admin.mobileAboutUsButton", "移动端关于我们按钮")}
                  </Label>
                </div>
                <p className="text-sm text-muted-foreground">
                  {settings?.about_us_button_mobile !== false
                    ? t("admin.mobileAboutUsEnabledDesc", "在移动端首页右上角显示")
                    : t("admin.mobileAboutUsDisabledDesc", "移动端首页不显示此按钮")}
                </p>
              </div>
              <Switch
                id="about-us-mobile"
                checked={settings?.about_us_button_mobile !== false}
                onCheckedChange={(checked) => updateSettings.mutateAsync({ about_us_button_mobile: checked })}
                disabled={updateSettings.isPending}
              />
            </div>

            {/* Voice Input Settings */}
            <div className="p-4 bg-muted/30 rounded-lg space-y-4">
              <div className="flex items-center gap-2">
                <Mic className="h-4 w-4" />
                <Label className="text-base font-medium">
                  {t("admin.voiceInputSettings", "语音输入")}
                </Label>
              </div>
              <p className="text-sm text-muted-foreground">
                {t("admin.voiceInputSettingsDesc", "控制AI助手语音输入按钮在不同设备上的显示")}
              </p>
              <div className="grid grid-cols-3 gap-4">
                <div className="flex flex-col items-center gap-2 p-3 bg-background rounded-lg border">
                  <Label htmlFor="voice-mobile" className="text-sm font-medium">
                    {t("admin.mobile", "手机")}
                  </Label>
                  <Switch
                    id="voice-mobile"
                    checked={settings?.voice_input_mobile !== false}
                    onCheckedChange={(checked) => updateSettings.mutateAsync({ voice_input_mobile: checked })}
                    disabled={updateSettings.isPending}
                  />
                </div>
                <div className="flex flex-col items-center gap-2 p-3 bg-background rounded-lg border">
                  <Label htmlFor="voice-tablet" className="text-sm font-medium">
                    {t("admin.tablet", "平板")}
                  </Label>
                  <Switch
                    id="voice-tablet"
                    checked={settings?.voice_input_tablet !== false}
                    onCheckedChange={(checked) => updateSettings.mutateAsync({ voice_input_tablet: checked })}
                    disabled={updateSettings.isPending}
                  />
                </div>
                <div className="flex flex-col items-center gap-2 p-3 bg-background rounded-lg border">
                  <Label htmlFor="voice-desktop" className="text-sm font-medium">
                    {t("admin.desktop", "桌面")}
                  </Label>
                  <Switch
                    id="voice-desktop"
                    checked={settings?.voice_input_desktop !== false}
                    onCheckedChange={(checked) => updateSettings.mutateAsync({ voice_input_desktop: checked })}
                    disabled={updateSettings.isPending}
                  />
                </div>
              </div>
            </div>

          </CardContent>
        </Card>

        {/* Ghost Restaurant Intro Content */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Ghost className="h-5 w-5" />
              {t("admin.ghostIntroContent", "幽灵餐厅介绍内容")}
            </CardTitle>
            <CardDescription>
              {t("admin.ghostIntroContentDesc", "编辑幽灵餐厅详情页「什么是幽灵餐厅」介绍区域的内容（每个餐厅可单独开关显示）")}
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-6">
            {/* Concept */}
            <div className="space-y-4">
              <Label className="text-base font-medium">{t("admin.ghostIntroConcept", "核心概念")}</Label>
              <div className="space-y-3">
                <div className="space-y-1">
                  <Label className="text-xs text-muted-foreground">中文</Label>
                  <Textarea value={ghostConceptZh} onChange={(e) => setGhostConceptZh(e.target.value)} rows={3} />
                </div>
                <div className="space-y-1">
                  <Label className="text-xs text-muted-foreground">English</Label>
                  <Textarea value={ghostConceptEn} onChange={(e) => setGhostConceptEn(e.target.value)} rows={3} />
                </div>
                <div className="space-y-1">
                  <Label className="text-xs text-muted-foreground">Português</Label>
                  <Textarea value={ghostConceptPt} onChange={(e) => setGhostConceptPt(e.target.value)} rows={3} />
                </div>
              </div>
            </div>

            {/* Advantages */}
            <div className="space-y-4">
              <Label className="text-base font-medium">{t("admin.ghostIntroAdvantages", "核心优势")}</Label>
              {ghostAdvantages.map((adv, idx) => (
                <div key={idx} className="p-4 bg-muted/30 rounded-lg space-y-3">
                  <p className="text-sm font-medium text-muted-foreground">{t("admin.advantage", "优势")} {idx + 1}</p>
                  <div className="grid gap-3 md:grid-cols-3">
                    <div className="space-y-1">
                      <Label className="text-xs text-muted-foreground">{t("admin.titleZh", "标题 (中文)")}</Label>
                      <Input value={adv.title_zh} onChange={(e) => {
                        const updated = [...ghostAdvantages];
                        updated[idx] = { ...updated[idx], title_zh: e.target.value };
                        setGhostAdvantages(updated);
                      }} />
                    </div>
                    <div className="space-y-1">
                      <Label className="text-xs text-muted-foreground">{t("admin.titleEn", "标题 (EN)")}</Label>
                      <Input value={adv.title_en} onChange={(e) => {
                        const updated = [...ghostAdvantages];
                        updated[idx] = { ...updated[idx], title_en: e.target.value };
                        setGhostAdvantages(updated);
                      }} />
                    </div>
                    <div className="space-y-1">
                      <Label className="text-xs text-muted-foreground">{t("admin.titlePt", "标题 (PT)")}</Label>
                      <Input value={adv.title_pt} onChange={(e) => {
                        const updated = [...ghostAdvantages];
                        updated[idx] = { ...updated[idx], title_pt: e.target.value };
                        setGhostAdvantages(updated);
                      }} />
                    </div>
                  </div>
                  <div className="grid gap-3 md:grid-cols-3">
                    <div className="space-y-1">
                      <Label className="text-xs text-muted-foreground">{t("admin.descZh", "描述 (中文)")}</Label>
                      <Textarea value={adv.desc_zh} onChange={(e) => {
                        const updated = [...ghostAdvantages];
                        updated[idx] = { ...updated[idx], desc_zh: e.target.value };
                        setGhostAdvantages(updated);
                      }} rows={2} />
                    </div>
                    <div className="space-y-1">
                      <Label className="text-xs text-muted-foreground">{t("admin.descEn", "描述 (EN)")}</Label>
                      <Textarea value={adv.desc_en} onChange={(e) => {
                        const updated = [...ghostAdvantages];
                        updated[idx] = { ...updated[idx], desc_en: e.target.value };
                        setGhostAdvantages(updated);
                      }} rows={2} />
                    </div>
                    <div className="space-y-1">
                      <Label className="text-xs text-muted-foreground">{t("admin.descPt", "描述 (PT)")}</Label>
                      <Textarea value={adv.desc_pt} onChange={(e) => {
                        const updated = [...ghostAdvantages];
                        updated[idx] = { ...updated[idx], desc_pt: e.target.value };
                        setGhostAdvantages(updated);
                      }} rows={2} />
                    </div>
                  </div>
                </div>
              ))}
            </div>

            <Button
              onClick={() => updateSettings.mutateAsync({
                ghost_intro_content: {
                  concept_zh: ghostConceptZh,
                  concept_en: ghostConceptEn,
                  concept_pt: ghostConceptPt,
                  advantages: ghostAdvantages,
                } as any,
              })}
              disabled={updateSettings.isPending}
            >
              {updateSettings.isPending ? <Loader2 className="w-4 h-4 animate-spin mr-2" /> : null}
              {t("admin.saveGhostIntro", "保存幽灵餐厅介绍")}
            </Button>
          </CardContent>
        </Card>

        {/* SEO Settings */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Globe className="h-5 w-5" />
              {t("admin.seoSettings", "SEO设置")}
            </CardTitle>
            <CardDescription>
              {t("admin.seoSettingsDescription", "设置网站标题和描述，这些内容会显示在浏览器标签和搜索引擎结果中")}
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-6">
            {/* Site Title - Multilingual */}
            <div className="space-y-4">
              <Label>{t("admin.siteTitle", "网站标题")}</Label>
              <div className="grid gap-4 md:grid-cols-1">
                <div className="space-y-2">
                  <Label className="text-xs text-muted-foreground">中文</Label>
                  <Input
                    value={siteTitleZh}
                    onChange={(e) => setSiteTitleZh(e.target.value)}
                    placeholder="小熊市场 - 发现美食之旅"
                  />
                </div>
                <div className="space-y-2">
                  <Label className="text-xs text-muted-foreground">English</Label>
                  <Input
                    value={siteTitleEn}
                    onChange={(e) => setSiteTitleEn(e.target.value)}
                    placeholder="Xiaoxiong Market - Discover Amazing Restaurants"
                  />
                </div>
                <div className="space-y-2">
                  <Label className="text-xs text-muted-foreground">Português</Label>
                  <Input
                    value={siteTitlePt}
                    onChange={(e) => setSiteTitlePt(e.target.value)}
                    placeholder="Xiaoxiong Market - Descubra Restaurantes Incríveis"
                  />
                </div>
              </div>
            </div>

            {/* Site Description - Multilingual */}
            <div className="space-y-4">
              <Label>{t("admin.siteDescription", "网站描述")}</Label>
              <div className="grid gap-4 md:grid-cols-1">
                <div className="space-y-2">
                  <Label className="text-xs text-muted-foreground">中文</Label>
                  <Textarea
                    value={siteDescriptionZh}
                    onChange={(e) => setSiteDescriptionZh(e.target.value)}
                    placeholder="探索精选餐厅，参与美食活动，与美食爱好者联系。"
                    rows={2}
                  />
                </div>
                <div className="space-y-2">
                  <Label className="text-xs text-muted-foreground">English</Label>
                  <Textarea
                    value={siteDescriptionEn}
                    onChange={(e) => setSiteDescriptionEn(e.target.value)}
                    placeholder="Explore curated restaurants, join exciting dining events, and connect with food lovers."
                    rows={2}
                  />
                </div>
                <div className="space-y-2">
                  <Label className="text-xs text-muted-foreground">Português</Label>
                  <Textarea
                    value={siteDescriptionPt}
                    onChange={(e) => setSiteDescriptionPt(e.target.value)}
                    placeholder="Explore restaurantes selecionados, participe de eventos gastronômicos."
                    rows={2}
                  />
                </div>
              </div>
            </div>

            <Button 
              onClick={handleSaveSEOSettings} 
              disabled={updateSettings.isPending}
              className="w-full md:w-auto"
            >
              {updateSettings.isPending && <Loader2 className="h-4 w-4 mr-2 animate-spin" />}
              {t("common.save")}
            </Button>
          </CardContent>
        </Card>

        <div className="grid gap-6 md:grid-cols-2">
          {/* Logo Settings */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Image className="h-5 w-5" />
                {t("admin.logoSettings")}
              </CardTitle>
              <CardDescription>{t("admin.logoSettingsDescription")}</CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              {/* Logo Preview */}
              <div className="flex flex-col items-center gap-4 p-6 border rounded-lg bg-muted/50">
                <Label className="text-sm text-muted-foreground">{t("admin.logoPreview")}</Label>
                <div className="bg-background p-4 rounded-lg shadow-sm border">
                  <img
                    src={currentLogo}
                    alt="Logo Preview"
                    style={{ width: `${localWidth}px`, height: `${localHeight}px` }}
                    className="object-contain"
                  />
                </div>
                <p className="text-xs text-muted-foreground">
                  {localWidth}px × {localHeight}px
                </p>
              </div>

              {/* Upload Logo */}
              <div className="space-y-2">
                <Label>{t("admin.uploadLogo")}</Label>
                <div className="flex gap-2">
                  <input
                    ref={logoInputRef}
                    type="file"
                    accept="image/*"
                    onChange={handleLogoUpload}
                    className="hidden"
                  />
                  <Button
                    variant="outline"
                    onClick={() => logoInputRef.current?.click()}
                    disabled={uploading}
                    className="flex-1"
                  >
                    {uploading ? (
                      <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                    ) : (
                      <Upload className="h-4 w-4 mr-2" />
                    )}
                    {t("admin.selectImage")}
                  </Button>
                  {settings?.logo_url && (
                    <Button
                      variant="destructive"
                      onClick={handleRemoveLogo}
                      disabled={updateSettings.isPending}
                    >
                      {t("common.remove")}
                    </Button>
                  )}
                </div>
              </div>

              {/* Logo Width */}
              <div className="space-y-3">
                <div className="flex justify-between items-center">
                  <Label>{t("admin.logoWidth")}</Label>
                  <div className="flex items-center gap-1">
                    <Input
                      type="number"
                      value={localWidth}
                      onChange={handleWidthInputChange}
                      onBlur={handleWidthInputBlur}
                      min={20}
                      max={100}
                      className="w-16 h-8 text-center text-sm"
                    />
                    <span className="text-sm text-muted-foreground">px</span>
                  </div>
                </div>
                <Slider
                  value={[localWidth]}
                  onValueChange={handleWidthChange}
                  onValueCommit={handleWidthCommit}
                  min={20}
                  max={100}
                  step={1}
                />
              </div>

              {/* Logo Height */}
              <div className="space-y-3">
                <div className="flex justify-between items-center">
                  <Label>{t("admin.logoHeight")}</Label>
                  <div className="flex items-center gap-1">
                    <Input
                      type="number"
                      value={localHeight}
                      onChange={handleHeightInputChange}
                      onBlur={handleHeightInputBlur}
                      min={20}
                      max={100}
                      className="w-16 h-8 text-center text-sm"
                    />
                    <span className="text-sm text-muted-foreground">px</span>
                  </div>
                </div>
                <Slider
                  value={[localHeight]}
                  onValueChange={handleHeightChange}
                  onValueCommit={handleHeightCommit}
                  min={20}
                  max={100}
                  step={1}
                />
              </div>
            </CardContent>
          </Card>

          {/* Favicon Settings */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Image className="h-5 w-5" />
                {t("admin.faviconSettings")}
              </CardTitle>
              <CardDescription>{t("admin.faviconSettingsDescription")}</CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              {/* Favicon Preview */}
              <div className="flex flex-col items-center gap-4 p-6 border rounded-lg bg-muted/50">
                <Label className="text-sm text-muted-foreground">{t("admin.faviconPreview")}</Label>
                <div className="bg-background p-4 rounded-lg shadow-sm border">
                  <img
                    src={settings?.favicon_url || "/favicon.png"}
                    alt="Favicon Preview"
                    className="w-8 h-8 object-contain"
                  />
                </div>
                <p className="text-xs text-muted-foreground">32px × 32px</p>
              </div>

              {/* Upload Favicon */}
              <div className="space-y-2">
                <Label>{t("admin.uploadFavicon")}</Label>
                <div className="flex gap-2">
                  <input
                    ref={faviconInputRef}
                    type="file"
                    accept="image/*"
                    onChange={handleFaviconUpload}
                    className="hidden"
                  />
                  <Button
                    variant="outline"
                    onClick={() => faviconInputRef.current?.click()}
                    disabled={uploadingFavicon}
                    className="flex-1"
                  >
                    {uploadingFavicon ? (
                      <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                    ) : (
                      <Upload className="h-4 w-4 mr-2" />
                    )}
                    {t("admin.selectImage")}
                  </Button>
                  {settings?.favicon_url && (
                    <Button
                      variant="destructive"
                      onClick={handleRemoveFavicon}
                      disabled={updateSettings.isPending}
                    >
                      {t("common.remove")}
                    </Button>
                  )}
                </div>
                <p className="text-xs text-muted-foreground">{t("admin.faviconHint")}</p>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Join Us Success Message Settings */}
        <Card ref={joinUsRef} id="join-us">
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <MessageSquare className="h-5 w-5" />
              {t("admin.joinUsSettings")}
            </CardTitle>
            <CardDescription>
              {t("admin.joinUsSettingsDescription")}
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-6">
            {/* Contact Phone */}
            <div className="space-y-2">
              <Label className="flex items-center gap-2">
                <Phone className="h-4 w-4" />
                {t("admin.joinContactPhone")}
              </Label>
              <Input
                value={joinPhone}
                onChange={(e) => setJoinPhone(e.target.value)}
                placeholder="968815050"
              />
            </div>

            {/* Success Title - Multilingual */}
            <div className="space-y-4">
              <Label>{t("admin.joinSuccessTitle")}</Label>
              <div className="grid gap-4 md:grid-cols-3">
                <div className="space-y-2">
                  <Label className="text-xs text-muted-foreground">中文</Label>
                  <Input
                    value={joinTitleZh}
                    onChange={(e) => setJoinTitleZh(e.target.value)}
                    placeholder="信息已提交成功！"
                  />
                </div>
                <div className="space-y-2">
                  <Label className="text-xs text-muted-foreground">English</Label>
                  <Input
                    value={joinTitleEn}
                    onChange={(e) => setJoinTitleEn(e.target.value)}
                    placeholder="Request Submitted!"
                  />
                </div>
                <div className="space-y-2">
                  <Label className="text-xs text-muted-foreground">Português</Label>
                  <Input
                    value={joinTitlePt}
                    onChange={(e) => setJoinTitlePt(e.target.value)}
                    placeholder="Informação Enviada com Sucesso!"
                  />
                </div>
              </div>
            </div>

            {/* Success Message - Multilingual */}
            <div className="space-y-4">
              <Label>{t("admin.joinSuccessMessage")}</Label>
              <div className="grid gap-4 md:grid-cols-1">
                <div className="space-y-2">
                  <Label className="text-xs text-muted-foreground">中文</Label>
                  <Textarea
                    value={joinMessageZh}
                    onChange={(e) => setJoinMessageZh(e.target.value)}
                    placeholder="感谢您的信任！我们的专属顾问将在24小时工作日内通过电话与您联系。"
                    rows={2}
                  />
                </div>
                <div className="space-y-2">
                  <Label className="text-xs text-muted-foreground">English</Label>
                  <Textarea
                    value={joinMessageEn}
                    onChange={(e) => setJoinMessageEn(e.target.value)}
                    placeholder="Thank you for your trust! Our dedicated consultant will contact you by phone within 24 business hours."
                    rows={2}
                  />
                </div>
                <div className="space-y-2">
                  <Label className="text-xs text-muted-foreground">Português</Label>
                  <Textarea
                    value={joinMessagePt}
                    onChange={(e) => setJoinMessagePt(e.target.value)}
                    placeholder="Obrigado pela sua confiança! Nosso consultor dedicado entrará em contato por telefone dentro de 24 horas úteis."
                    rows={2}
                  />
                </div>
              </div>
            </div>

            <Button 
              onClick={handleSaveJoinSettings} 
              disabled={updateSettings.isPending}
              className="w-full md:w-auto"
            >
              {updateSettings.isPending && <Loader2 className="h-4 w-4 mr-2 animate-spin" />}
              {t("common.save")}
            </Button>
          </CardContent>
        </Card>

        {/* Contact Info Settings */}
        <Card ref={contactInfoRef} id="contact-info">
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Phone className="h-5 w-5" />
              {t("admin.contactSettings")}
            </CardTitle>
            <CardDescription>
              {t("admin.contactSettingsDescription")}
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-6">
            <div className="grid gap-4 md:grid-cols-2">
              <div className="space-y-2">
                <Label className="flex items-center gap-2">
                  <Phone className="h-4 w-4" />
                  {t("admin.contactPhone")}
                </Label>
                <Input
                  value={contactPhone}
                  onChange={(e) => setContactPhone(e.target.value)}
                  placeholder="+351 968 815 050"
                />
              </div>
              <div className="space-y-2">
                <Label className="flex items-center gap-2">
                  <Mail className="h-4 w-4" />
                  {t("admin.contactEmail")}
                </Label>
                <Input
                  type="email"
                  value={contactEmail}
                  onChange={(e) => setContactEmail(e.target.value)}
                  placeholder="comercial@xiaoxiong.pt"
                />
              </div>
            </div>

            <Button 
              onClick={handleSaveContactInfo} 
              disabled={updateSettings.isPending}
              className="w-full md:w-auto"
            >
              {updateSettings.isPending && <Loader2 className="h-4 w-4 mr-2 animate-spin" />}
              {t("common.save")}
            </Button>
          </CardContent>
        </Card>
      </div>
    </>
  );
};

export default AdminSiteSettings;
